import Snake from './snake'
import Point from './point'
import './index.css'

const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')
canvas.width = window.innerWidth / 2
canvas.height = window.innerHeight / 2

const snake = new Snake(canvas, 50, 300)
let point = new Point(
  canvas,
  Math.floor(Math.random() * (innerWidth / 2 - 10) + 5),
  Math.floor(Math.random() * (innerHeight / 2 - 10) + 5)
)
let direction

const animate = e => {
  requestAnimationFrame(animate)
  c.clearRect(0, 0, innerWidth, innerHeight)
  point.draw()
  snake.move(direction)
  if (getDistance(snake.x, snake.y, point.x, point.y) < 10) {
    point.reDraw()
    snake.levelUp()
  }
}

const getDistance = (x1, y1, x2, y2) => {
  let xDistance = x2 - x1
  let yDistance = y2 - y1
  return Math.sqrt(Math.pow(xDistance, 2) + Math.pow(yDistance, 2))
}

const setDirection = e => (direction = e.keyCode)
window.addEventListener('keydown', setDirection)
requestAnimationFrame(animate)



