class Point {
  constructor(canvas, width, height) {
    this.x = width
    this.y = height
    this.c = canvas.getContext('2d')
  }

  draw() {
    this.c.fillRect(this.x, this.y, 10, 10)
  }

  reDraw() {
    this.x = Math.floor(Math.random() * (innerWidth / 2 - 10) + 5)
    this.y = Math.floor(Math.random() * (innerHeight / 2 - 10) + 5)
  }
}

export default Point



