class Snake {
  constructor(canvas, width, height) {
    this.x = width
    this.y = height
    this.speed = 1
    this.c = canvas.getContext('2d')
  }

  draw() {
    this.c.fillRect(this.x, this.y, 10, 10)
  }

  move(direction) {
    this.draw()
    switch (direction) {
      case 37:
        this.x -= this.speed
        break
      case 38:
        this.y -= this.speed
        break
      case 39:
        this.x += this.speed
        break
      case 40:
        this.y += this.speed
    }
  }

  levelUp() {}
}

export default Snake



